import React from "react";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";
import {
  Users,
  UserCheck,
  TrendingUp,
  TrendingDown,
  Plus,
  LucideIcon,
} from "lucide-react";
import { format } from "date-fns";
import { dashboardAPI } from "../services";
import LoadingSpinner from "../components/LoadingSpinner";
import {
  STATUS_BADGE_CLASSES,
  PRIORITY_BADGE_CLASSES,
} from "../utils/constants";
import type { Lead, Client } from "../types";

interface StatCard {
  name: string;
  value: number;
  icon: LucideIcon;
  color: string;
  href?: string;
}

const Dashboard: React.FC = () => {
  const { data, isLoading, error } = useQuery(
    "dashboardStats",
    dashboardAPI.getStats as any
  );

  if (isLoading) return <LoadingSpinner />;

  if (error) {
    return (
      <div className="text-center py-12">
        <p className="text-red-600">Erro ao carregar dados do dashboard</p>
      </div>
    );
  }

  const {
    recentLeads = [],
    recentClients = [],
    stats,
  } = (data as any)?.data || {};

  const statCards: StatCard[] = [
    {
      name: "Total de Leads",
      value: stats?.totalLeads || 0,
      icon: Users,
      color: "bg-blue-500",
      href: "/leads",
    },
    {
      name: "Total de Clientes",
      value: stats?.totalClients || 0,
      icon: UserCheck,
      color: "bg-green-500",
      href: "/clients",
    },
    {
      name: "Leads Conquistados",
      value: stats?.wonLeads || 0,
      icon: TrendingUp,
      color: "bg-teal-500",
    },
    {
      name: "Leads Perdidos",
      value: stats?.lostLeads || 0,
      icon: TrendingDown,
      color: "bg-red-500",
    },
  ];

  const getStatusBadge = (status: string): string => {
    return (
      STATUS_BADGE_CLASSES[status as keyof typeof STATUS_BADGE_CLASSES] ||
      STATUS_BADGE_CLASSES.new
    );
  };

  const getPriorityBadge = (priority: string): string => {
    return (
      PRIORITY_BADGE_CLASSES[priority as keyof typeof PRIORITY_BADGE_CLASSES] ||
      PRIORITY_BADGE_CLASSES.medium
    );
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
        <div className="flex space-x-3">
          <Link to="/leads" className="btn btn-secondary">
            <Plus className="h-4 w-4 mr-2" />
            Novo Lead
          </Link>
          <Link to="/clients" className="btn btn-primary">
            <Plus className="h-4 w-4 mr-2" />
            Novo Cliente
          </Link>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {statCards.map((stat) => {
          const Icon = stat.icon;
          return (
            <div key={stat.name} className="card">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div
                    className={`w-12 h-12 ${stat.color} rounded-lg flex items-center justify-center`}
                  >
                    <Icon className="h-6 w-6 text-white" />
                  </div>
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-500">
                    {stat.name}
                  </p>
                  <p className="text-2xl font-semibold text-gray-900">
                    {stat.value}
                  </p>
                </div>
              </div>
              {stat.href && (
                <div className="mt-4">
                  <Link
                    to={stat.href}
                    className="text-sm text-teal-600 hover:text-teal-500 font-medium"
                  >
                    Ver todos →
                  </Link>
                </div>
              )}
            </div>
          );
        })}
      </div>

      {/* Conversion Rate */}
      {stats?.conversionRate && (
        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-2">
            Taxa de Conversão
          </h3>
          <div className="flex items-center">
            <div className="flex-1 bg-gray-200 rounded-full h-2">
              <div
                className="bg-teal-600 h-2 rounded-full"
                style={{ width: `${stats.conversionRate}%` }}
              ></div>
            </div>
            <span className="ml-3 text-sm font-medium text-gray-900">
              {stats.conversionRate}%
            </span>
          </div>
        </div>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Leads */}
        <div className="card">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-gray-900">
              Leads Recentes
            </h3>
            <Link
              to="/leads"
              className="text-sm text-teal-600 hover:text-teal-500 font-medium"
            >
              Ver todos
            </Link>
          </div>
          <div className="space-y-3">
            {recentLeads.length > 0 ? (
              recentLeads.map((lead: Lead) => (
                <div
                  key={lead._id}
                  className="flex items-center justify-between p-3 border border-gray-200 rounded-lg"
                >
                  <div className="flex-1">
                    <Link
                      to={`/leads/${lead._id}`}
                      className="font-medium text-gray-900 hover:text-teal-600"
                    >
                      {lead.name}
                    </Link>
                    <p className="text-sm text-gray-500">{lead.email}</p>
                    <div className="flex items-center space-x-2 mt-1">
                      <span className={`badge ${getStatusBadge(lead.status)}`}>
                        {lead.status}
                      </span>
                      <span
                        className={`badge ${getPriorityBadge(lead.priority)}`}
                      >
                        {lead.priority}
                      </span>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className="text-xs text-gray-500">
                      {format(new Date(lead.createdAt), "MMM dd")}
                    </p>
                  </div>
                </div>
              ))
            ) : (
              <p className="text-gray-500 text-center py-4">
                Nenhum lead recente
              </p>
            )}
          </div>
        </div>

        {/* Recent Clients */}
        <div className="card">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-gray-900">
              Clientes Recentes
            </h3>
            <Link
              to="/clients"
              className="text-sm text-teal-600 hover:text-teal-500 font-medium"
            >
              Ver todos
            </Link>
          </div>
          <div className="space-y-3">
            {recentClients.length > 0 ? (
              recentClients.map((client: Client) => (
                <div
                  key={client._id}
                  className="flex items-center justify-between p-3 border border-gray-200 rounded-lg"
                >
                  <div className="flex-1">
                    <Link
                      to={`/clients/${client._id}`}
                      className="font-medium text-gray-900 hover:text-teal-600"
                    >
                      {client.name}
                    </Link>
                    <p className="text-sm text-gray-500">{client.email}</p>
                  </div>
                  <div className="text-right">
                    <p className="text-xs text-gray-500">
                      {format(new Date(client.createdAt), "MMM dd")}
                    </p>
                  </div>
                </div>
              ))
            ) : (
              <p className="text-gray-500 text-center py-4">
                Nenhum cliente recente
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
